var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('main',[_c('h1',{attrs:{"id":"title"}},[_vm._v(" Recommendations for the University of Illinois Archives on the Handling of the Doris Duke Oral History Collection ")]),_c('h2',{attrs:{"id":"byline"}},[_vm._v("Ben Ostermeier")]),_c('article',[_c('section',{attrs:{"id":"1-background-information"}},[_c('h2',[_vm._v("Background Information")]),_c('p',[_vm._v(" For thousands of years, the land that we know of today as the state of Illinois was home to many indigenous people deserving of our respect and acknowledgement. The campus of the University of Illinois at Urbana-Champaign (UIUC) in particular is the land of the Peoria, Kaskaskia, Piankashaw, Wea, Miami, Mascoutin, Odawa, Sauk, Mesquaki, Kickapoo, Potawatomi, Ojibwe, and Chickasaw Nations."),_c('footnote',{attrs:{"text":"Office of the Chancellor, \"Land Acknowledgement Statement,\" 2018, <a href=\"https://chancellor.illinois.edu/land_acknowledgement.html\">https://chancellor.illinois.edu/land_acknowledgement.html</a>.","fid":"chancellor-1","footnoteNumber":_vm.footnotes.find(function (obj) {
              return obj.fid === 'chancellor-1';
            }).footnoteNumber},on:{"reference-clicked":_vm.slideToElement,"footnote-added":_vm.addFootnote}})],1),_c('p',[_vm._v(" The arrival of Anglo-American settlers to Illinois in the late 18th and early 19th centuries brought a violent, oppressive colonialism in which white Americans brutally attacked and killed the idigenous peoples of Illinois. The survivors of this vicious ethnic cleansing were forcefully removed to regions outside of the state in a process so thorough that no Native American reservations exist in the state of Illinois today."),_c('footnote',{attrs:{"text":"James E. Davis, <em>Frontier Illinois</em>. (Bloomington: Indiana University Press, 1998), 198-199.","fid":"davis-1","footnoteNumber":_vm.footnotes.find(function (obj) {
              return obj.fid === 'davis-1';
            }).footnoteNumber},on:{"reference-clicked":_vm.slideToElement,"footnote-added":_vm.addFootnote}})],1),_c('p',[_vm._v(" The forced removal of indigenous peoples throughout the United States allowed the federal government to sell land previously home to Native Americans to white Americans. The federal government then set aside profits from these land sales to fund the establishment of universities. One of these institutions was UIUC, established in 1867 to promote the “civilizing” process that would transform young Americans into “industrious, civilized” adults, a critical part of the colonialist process."),_c('footnote',{attrs:{"text":"Robert Lee, Tristan Ahtone, Margaret Pearce, Kalen Goodluck, Geoff McGhee, Cody Leff, Katherine Lanpher, and Taryn Salinas, “Land-Grab Universities,” <em>High Country News</em>, March 30, 2020, <a href='https://www.landgrabu.org/'>https://www.landgrabu.org/</a>.","fid":"lee-1","footnoteNumber":_vm.footnotes.find(function (obj) {
              return obj.fid === 'lee-1';
            }).footnoteNumber},on:{"reference-clicked":_vm.slideToElement,"footnote-added":_vm.addFootnote}})],1),_c('p',[_vm._v(" In 1966, about a century after UIUC was established, tobacco heiress and philanthropist Doris Duke began awarding grants to universities to collect oral histories from Indigenous peoples throughout the United States."),_c('footnote',{attrs:{"text":"Roth-Schrefer, Kristin and Dryfoos, Delaney, “DDCF Announces the Doris Duke Native Oral History Revitalization Project,” press release, February 9, 2021, <a href='https://www.ddcf.org/globalassets/child-well-being/press-releases/dd-native-oral-history-revitalization-project-launch.pdf'>https://www.ddcf.org/globalassets/child-well-being/press-releases/dd-native-oral-history-revitalization-project-launch.pdf</a>.","fid":"schrefer-1","footnoteNumber":_vm.footnotes.find(function (obj) {
              return obj.fid === 'schrefer-1';
            }).footnoteNumber},on:{"reference-clicked":_vm.slideToElement,"footnote-added":_vm.addFootnote}}),_vm._v(" One of these grants was awarded to the anthropology department of UIUC. The oral history program began at UIUC the following year, 1967, under the direction of anthropology professor Edward Bruner. Alongside additional anthropology professors, the program had input from three historians. However, the bulk of the oral history collection and community engagement was undertaken by project field workers, who were predominantly anthropology graduate students."),_c('footnote',{attrs:{"text":"Bruner, Edward, <em>The Duke Indian Oral History Program: The University of Illinois</em>, report, 1995.","fid":"bruner-1","footnoteNumber":_vm.footnotes.find(function (obj) {
              return obj.fid === 'bruner-1';
            }).footnoteNumber},on:{"reference-clicked":_vm.slideToElement,"footnote-added":_vm.addFootnote}})],1),_c('p',[_vm._v(" Possibly the greatest challenge faced by this program is the absence of any Native American reservations in Illinois, a long standing consequence of how the state was established. According to a report on the project written by Bruner in 1995, there were few Indigenous Americans enrolled at the university in the 1960s, and the project identified the Native American community in Chicago as the only significant Native American community in the state. As a result, field workers had to travel to communities geographically distant from the campus in Champaign-Urbana. Instead of the university or the anthropology department developing a cooperative, reciprocal relationship with a smaller, local Indigenous community, it became the responsibility of individual field workers to build relationships with Indigenous communities."),_c('footnote',{attrs:{"text":"Bruner, Edward, <em>The Duke Indian Oral History Program</em>.","fid":"bruner-2","footnoteNumber":_vm.footnotes.find(function (obj) {
              return obj.fid === 'bruner-2';
            }).footnoteNumber},on:{"reference-clicked":_vm.slideToElement,"footnote-added":_vm.addFootnote}})],1),_c('p',[_vm._v(" The results of this strategy were mixed. Graduate student field workers received specialized instruction from anthropology faculty and worked under their supervision. However, geographic distance and the comparatively limited communication technologies of the 1960s and ‘70s meant that field workers had a great deal of individual responsibility to develop relationships with the Native American community they were working with. As a result, the quality and form of the oral histories and other collected materials varies widely, with Bruner’s report describing some projects as largely successful while stating that others “can only be classified as failures.” The report does not go into extensive detail into the nature of these failures, but it does note that in some cases the project withdrew financial support from the field worker. ")]),_c('p',[_vm._v(" In most cases, field workers spent several months or even a year establishing relationships with the Indigenous community. Many of these field workers had particular anthropological interests that may not have prioritized the collection of archival material. By and large, the oral histories and other materials focus on depth and intimacy rather than a broad scope of information about the Indigenous community."),_c('footnote',{attrs:{"text":"Bruner, Edward, <em>The Duke Indian Oral History Program</em>.","fid":"bruner-3","footnoteNumber":_vm.footnotes.find(function (obj) {
