            }).footnoteNumber},on:{"reference-clicked":_vm.slideToElement,"footnote-added":_vm.addFootnote}})],1),_c('p',[_vm._v(" After the program concluded in the early 1970s, the collection was physically housed with the anthropology department for several decades. In 2002, Anthropology Department Chair Brenda Farnell approached the University Archive about transferring ownership of the Doris Duke collection to the archive. After agreeing to house the materials, the University Archive had to decide how accessible the collection should be to the general public. After consulting the code of ethics for both the Society of American Archivists (SAA) and the American Anthropological Association that existed at the time, University Archives decided to make the materials accessible to the general public with some level of restriction for particular materials in the collection. University Archives reached out to the original field researchers to establish what level of access should be established for the materials in the Duke Collection."),_c('footnote',{attrs:{"text":"Bruner, Edward, <em>The Duke Indian Oral History Program</em>.","fid":"bruner-4","footnoteNumber":_vm.footnotes.find(function (obj) {
