            }).footnoteNumber},on:{"reference-clicked":_vm.slideToElement,"footnote-added":_vm.addFootnote}})],1),_c('p',[_vm._v(" After hearing back from most of the field researchers, in 2003 the University Archives established an access policy for the materials with three levels of access for different parts of the collection: ")]),_vm._m(0),_c('p',[_vm._v(" Additionally, any PII was restricted, and researchers using the materials were required to respect the anonymity of any individuals represented in the materials."),_c('footnote',{attrs:{"text":"Anderson and Prom “Class Visit,” 2021.","fid":"anderson-2","footnoteNumber":_vm.footnotes.find(function (obj) {
