            }).footnoteNumber},on:{"reference-clicked":_vm.slideToElement,"footnote-added":_vm.addFootnote}}),_vm._v(" University Archives has begun a two year project to work with its Duke collection, making it accessible online through partnerships with the tribal groups represented in the collection."),_c('footnote',{attrs:{"text":"Nicole Piwowarczyk, <em>Anderson, Prom, and Davis Awarded Doris Duke Indian Oral History Program Archives: Revitalization and Community Building Grant</em>, press release, February 22, 2021, <a href='http://publish.illinois.edu/library-excellence/2021/02/22/anderson-prom-and-davis-awarded-doris-duke-indian-oral-history-program-archives-revitalization-and-community-building-grant/'>http://publish.illinois.edu/library-excellence/2021/02/22/anderson-prom-and-davis-awarded-doris-duke-indian-oral-history-program-archives-revitalization-and-community-building-grant/</a>.","fid":"piwowarczyk-1","footnoteNumber":_vm.footnotes.find(function (obj) {
