var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('ol',[_c('li',[_vm._v(" Level 1 Open: 5 of the projects in which field researchers said they and the tribe they were working with were comfortable with the materials being accessible to the general public. ")]),_c('li',[_vm._v(" Level 2 Restricted: 21 of the projects, where patrons could access the materials after completing an access request form. Patrons agreed to not release any sensitive or publicly identifiable (PII) information. This level of access was established for the large majority of the collection. ")]),_c('li',[_vm._v(" Level 3 Confidential: Field notes written by field researchers from each of the projects and a research paper for one of the projects. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('ul',[_c('li',[_vm._v("Field notes")]),_c('li',[_vm._v("Interview tapes")]),_c('li',[_vm._v("Interview transcripts")]),_c('li',[_vm._v("Photographs")]),_c('li',[_vm._v(" Documents, including newspaper clippings, informational pamphlets, official documents, and other papers related to Native American communities ")]),_c('li',[_vm._v(" PhD dissertations and other academic papers written by the field workers ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{attrs:{"id":"4-my-recomendations"}},[_c('h2',[_vm._v("My Recommendations")]),_c('p',[_vm._v(" Given the sensitive and intimate nature of many of the materials, and the colonialist underpinnings of the original gathering of the materials in the ‘60s and ‘70s, I am glad that the University Archives team under the leadership of Chris Prom and Bethany Anderson are seemingly considering the numerous ethical considerations of this project. They appear to recognize the project’s major flaw: that indigenous community members had little involvement as co-creators, but were instead treated in some cases like ‘lab’ subjects to be studied. Still, there is tremendous historic and cultural value to the lasting materials that were generated by this project, and the primary benefactors of that legacy ought to be the tribes themselves. ")]),_c('p',[_vm._v(" The revitalization project is still in its early stages, and no doubt numerous issues will emerge as they move forward. It seems that a lot of the work at this stage is simply doing a better job identifying the materials in the collection in order to facilitate a conversation with tribal representatives. Still it is critical that contemporary native voices are included in the conversation around these materials as soon as it is feasible. ")]),_c('p',[_vm._v(" My particular recommendation relates to the use of transcripts. I asked in class what the project was planning on doing in regards to oral history transcripts. Chris Prom stated that the original transcripts were likely done by the field researchers, and Bethany Anderson said that part of the grant project will support the transcription of tapes that have previously not been transcribed. She also said they may want to redo or enhance existing transcripts in some form, but it is unclear at this stage as they still need to take a closer look at the original tapes and the transcripts first. ")]),_c('p',[_vm._v(" Chris Prom also indicated that, as best he could tell when reviewing access to the collection since 2003, all of the researchers who have looked at the collection have referred to the transcript instead of the original audio tapes. Part of this may be because of concerns that listening directly to the audio may be cumbersome and even put the physical tape at risk. However, it is my opinion that the preference towards textual transcripts over the spoken word is part of a Western-centric legacy that privileges literacy over more natural or “primitive” forms of communication like the spoken word. In my experience in working with oral histories, I prefer to think of the ‘original’ source as the audio itself and view the transcript as a useful, though imperfect derivative. No doubt transcripts are critical especially for accessibility reasons, as well as for searchability and usability. Still, as an experienced transcriber, I can testify that a transcriber cannot 100% recreate the spoken word as a transcript. When I transcribe, I often have to make decisions about whether to include jumbled phrases, verbal ticks like “um”s, or other forms of communication that are not typically written when people communicate through text. No doubt a transcriber has various forms of implicit bias that may cause them to transcribe an indigenous person in particular ways that are not flattering. ")]),_c('p',[_vm._v(" Since these transcripts were presumably done by field researchers in the ‘60s and ‘70s, I am somewhat concerned that the transcripts have several problems that the project team will need to review as part of the project. My particular recommendation, therefore, is for them to compare the transcripts to the original audio to ensure that the transcripts are not only accurate, but also do not reinforce forms of bias against indigenous peoples. Most especially, contemporary indigenous peoples must be involved in this transcript-review process in some form. ")])])}]

